import { memo, useState } from "react";
import './Reapeted.css';
import ReapetedArray from '../../Data/Reapeted';
import { SectionTitle } from "../../component";
import { FaArrowRight } from "react-icons/fa";
const Reapeted = () => {
    const [array, setArray] = useState(ReapetedArray);

    function toggleShowAnswer(id) {
        setArray(prev => {
            return prev.map(answer => {
                if (answer.id === id) {
                    return {
                        ...answer,
                        design: {
                            ...answer.design,
                            height: answer.toggleAnswer ? "0px" : "100%",
                            opacity: answer.toggleAnswer ? 0 : 1,
                            rotated: {
                                transform: answer.toggleAnswer ? "rotate(0deg)" : "rotate(90deg)"
                            },
                            displayed: {
                                display: answer.toggleAnswer ? "none" : "block"
                            }
                        },
                        toggleAnswer: !answer.toggleAnswer
                    };
                } else {
                    return answer; // Keep other answers unchanged
                }
            });
        });
    }
    return (
        <>
            <div className="common-questions mt-20">
                <SectionTitle title={'FAQ'} />
                <div className="bg-red-600 mt-10 pt-4">
                    <div className="container">
                        <div className="row flex-wrap items-center">
                            {
                                array.map((data, index) => {
                                    return (
                                        <div key={index} className="question-card lg:w-1/2 md:w-3/4 md:m-auto sm:w-11/12 sm:m-auto  px-2 mb-3">
                                            <div className="question" onClick={() => toggleShowAnswer(data.id)}>
                                                <p className="text-white cursor-pointer">
                                                    {data.question}
                                                    <FaArrowRight style={data.design.rotated} className="inline ml-1 transition-all" />
                                                </p>
                                            </div>
                                            <div className="answer px-2 text-sm bg-slate-50 rounded-md mt-1" style={data.design}>
                                                <p className="text-slate-900 mb-0 p-2" style={data.design.displayed}>{data.answer}</p>
                                            </div>
                                        </div>
                                    );
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default memo(Reapeted);